@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
}

h1, h2, h3 {
  font-family: 'Roboto Mono', monospace;
}

.about {
  padding-top: 5%;
}

/* .projects {
  background: rgb(246,38,46);
  background: linear-gradient(90deg, rgba(246,38,46,1) 13%, rgba(251,120,64,1) 90%);
} */

.project-img {
  box-shadow: 5px 5px 5px #878787;
}

.card-link {
  text-align: center;
  text-decoration: none;
  color: red
}

.header {
  padding: 80px;
  text-align: center;
  color: black;
  font-family: 'Roboto Mono', monospace;
  background-size: cover;
  background: rgb(246,38,46);
  background: linear-gradient(90deg, rgba(246,38,46,1) 13%, rgba(251,120,64,1) 90%);
}

.header h1 {
  font-size: 40px;
}

.navbar {
  overflow: hidden;
  background: rgb(246,38,46);
  background: linear-gradient(90deg, rgba(246,38,46,1) 13%, rgba(251,120,64,1) 90%); 
}

.navbar a {
  float: left;
  color: white;
  text-align: center;
  padding: 5px 10px;
}

.navbar a.right {
  float: right;
}

.navbar button {
  border: none;
  padding: 10px;
  width: 80px;
  background-color: #000000;
  color: #ffffff;
  border-radius: 5%;
  font-size: 1rem;
}

.navbar button:hover {
  background-color: #ffffff;
  color: black;
}

.row {
  display: flex;
}

.side {
  flex: 30% 1;
  background-color: #f1f1f1;
  padding: 20px;
}

.main {
  flex: 70% 1;
  background-color: white;
  padding: 20px;
}

.fakeimg {
  width: 100%;
  max-height: 300px;
  object-fit: cover;
  border-radius: 4px;
}

.footer {
  padding: 20px;
  text-align: center;
  background: #ddd;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  transition: all 0.5s ease;
  animation: smoothScroll 1s forwards;
}

@keyframes smoothScroll {
  0% {
    transform: translateY(-142px);
  }

  100% {
    transform: translateY(0px);
  }
}
